@import url(https://fonts.googleapis.com/css?family-Poppins:400,500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn-preclick {
  background: none;
  border: none;
}

.btn-postclick {
  background: none;
  border: none;
}

.btn-preclick:hover,
.btn-postclick:hover {
  color: #fff;
  background: none;
}

.btn-postclick:focus,
.btn-postclick.focus {
  color: #fff;
  background: none;
  box-shadow: 0 0 0 0.2rem rgba(60, 198, 171, 0.5);
}

.form-custom:focus {
  border-color: white;
  color: #495057;
  background-color: #fff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(26, 188, 156, 0.25);
}

@charset "UTF-8";

body {
  padding: 0;
  margin: 0;
  background-color: #8ad7f5;
  font-family: Poppins, sans-serif;
}

header section footer {
  margin-bottom: 100px;
  display: block;
}

.nav-custom {
  outline: 1px solid white;
  background: none;
}

.navbar-expand-lg {
  border: 1px solid white;
  background: none;
}

.navbar-open {
  border: 1px solid white;
}

.navbar-close {
  border: none;
}

.custom-nav-link {
  color: white;
}

.title {
  font-weight: 500;
  font-size: 3em;
  margin-bottom: 1rem;
  padding: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  display: block;
}

.landing-wrapper {
  padding-top: 6rem;
  padding-bottom: calc(6rem + 74px);
  display: flex;
  align-items: center;
  height: 90vh;
}

.about-wrapper {
  align-items: center;
}

.about-image {
  position: relative;
  display: block;
  max-width: 25rem;
  object-fit: cover;
  border-radius: 1rem;
  border: 3px solid #021a40;
  overflow: hidden;
  background-color: rgba(26, 188, 156, 0.9);
  margin: 20px auto;
}

.about-text {
  padding: 50px, auto;
}

.button-resume {
  background-color: green;
}

@charset "UTF-8";

.portfolio-item {
  margin: 20px 0px;
}
.portfolio-image-parent {
  height: 260px;
  width: 260px;
  object-fit: contain;
  border-radius: 1rem;
  border: 3px solid #021a40;
  background-color: white;
  margin: 0px 20px;
}

.portfolio-image {
  position: relative;
  display: block;
  width: 95%;
  height: 95%;
  overflow: hidden;
  object-fit: contain;
  background-position: center;
  margin-left: auto;
  margin-right: auto;
}

h4 {
  font-size: 1.2em;
}

a + a {
  margin-left: 50px;
}

span {
  margin: 2px 0px;
  display: inline-block;
}

@charset "UTF-8";

.interest-item {
  cursor: pointer;
  position: relative;
  display: block;
  max-width: 25rem;
  border-radius: 0.5rem;
  overflow: hidden;
}
.interest-item .interest-item-caption {
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.4s ease-in-out;
  opacity: 0;
  background-color: black;
}

.interest-item-caption {
  padding: 10px;
}

.interest-item .interest-item-caption:hover {
  opacity: 0.8;
}

.interest-item .interest-item-caption .interest-item-caption-content {
  font-size: 1.5rem;
}

.interest-title {
  margin-top: 20px;
  display: block;
}

